import { observable, action, toJS } from "mobx";
import DataApiService from "@/service/http";
import storage from "@/utils/storage";

class Store {
  constructor(props: any) {
    this.tokenContext = props.tokenContext;
  }
  tokenContext: IToken;
  NUM_ROWS = 10;
  @observable data: any[] = [];
  @observable followData: object = {};
  @observable pageNumber: number = 0;
  @observable isLoading: boolean = false;
  @observable hasMore: boolean = true;

  @action.bound
  reset() {
    this.data = [];
    this.followData = {};
    this.pageNumber = 0;
    this.isLoading = false;
    this.hasMore = true;
  }

  @action.bound
  fetchData(): Promise<any> {
    this.isLoading = true;
    const userId = storage.getItem("userId");
    return DataApiService.getShowTableData({
      tableName: "view_my_defense_area_information",
      length: this.NUM_ROWS,
      start: this.pageNumber * this.NUM_ROWS,
      type: "TABLE_QUERY",
      params: {
        filterCondition: [
          {
            type: "1",
            field: "view_my_defense_area_information.userId",
            condition: "=",
            connection: "&amp;&amp;",
            id: 1,
            pid: 0,
            value: userId
          }
        ],
        orderBy: [
          {
            fieldName: "view_my_defense_area_information.name",
            sort: "DESC"
          }
        ]
      },
      fieldName: [
        "view_my_defense_area_information.id",
        "view_my_defense_area_information.name",
        "view_my_defense_area_information.status",
        "view_my_defense_area_information.mainFrameName",
        "view_my_defense_area_information.areaLocationName",
        "view_my_defense_area_information.followId"
      ]
    })
      .then((res: any) => {
        const { result } = res;
        const data = [...toJS(this.data), ...result.aaData];
        const followData = {};
        if (data.length >= result.iTotalRecords) {
          this.hasMore = false;
        }
        data.map(d => {
          followData[d.view_my_defense_area_information_id] = d.view_my_defense_area_information_followId || null;
        });
        this.data = data;
        this.pageNumber += 1;
        return { data, followData };
      })
      .finally(() => (this.isLoading = false));
  }

  @action.bound
  addDeviceFollow(device_id: string): Promise<any> {
    return DataApiService.addDeviceFollow({
      device_id
    });
  }

  @action.bound
  deleteDeviceFollow(followId: string): Promise<any> {
    return DataApiService.deleteDeviceFollow({ ids: [followId] });
  }

  @action deleteDeviceData = (device_id: string, cb: Function) => {
    this.data = this.data.filter(item => item.view_my_defense_area_information_id !== device_id);
    cb && cb(toJS(this.data));
  };
}

export default Store;
