import React, { useContext } from 'react';
import Store from './store';
import Follow from './follow';
import { TokenContext } from '@/pages/_internal/app/middlewares/AppAuthorized';
require('./style.scss');

export default (props: any) => {
    const tokenContext = useContext(TokenContext);
    const store = new Store({ tokenContext });
    return <Follow store={store} history={props.history} />;
};
