import React, { useEffect, useState } from "react";
import { observer } from "mobx-react";
import { ListView, Toast, Icon } from "antd-mobile";
import SubPage from "@/components/SubPage";
import { Item, State } from "@/components/ProtectionList";
import DefenseStatus from "@/components/DefenseStatus";
import StarSwitch from "@/components/StarSwitch";
import EasyNarBar from "@/components/EasyNarBar";

const EMPTY_TEXT = "无";
export default observer((props: any) => {
  const { store, history } = props;
  const { isLoading, hasMore, fetchData, addDeviceFollow, deleteDeviceFollow, deleteDeviceData, reset, NUM_ROWS } = store;

  useEffect(() => {
    getData();
    return () => {
      reset();
    };
  }, []);

  const [followData, setFollowData] = useState({});
  const [following, setFollowimg] = useState(false);
  const [dataSource, setDataSource] = useState(
    new ListView.DataSource({
      rowHasChanged: (row1: any, row2: any) => row1 !== row2
    })
  );

  const getData = () => {
    fetchData()
      .then((res: { followData: React.SetStateAction<{}>; data: any }) => {
        setFollowData(res.followData);
        setDataSource(dataSource.cloneWithRows(res.data));
      })
      .catch(() => {
        Toast.info("请求失败", 1);
      });
  };

  const onEndReached = () => {
    if (!isLoading && hasMore) {
      getData();
    }
  };

  const renderFooter = () => {
    if (isLoading) {
      return <div style={{ padding: 30, textAlign: "center" }}>加载中...</div>;
    } else if (!hasMore) {
      return <div style={{ padding: 30, textAlign: "center" }}>加载完毕</div>;
    } else {
      return <div style={{ padding: 30, textAlign: "center" }}>暂无数据</div>;
    }
  };

  const addFollow = (userId: string) => {
    if (following) return;
    setFollowimg(true);
    addDeviceFollow(userId)
      .then((res: { result: any }) => {
        Toast.info("关注成功", 1);
        changeFollow(userId, res.result);
      })
      .finally(() => {
        setFollowimg(false);
      });
  };
  const deleteFollow = (userId: string, followId: string) => {
    if (following) return;
    setFollowimg(true);
    deleteDeviceFollow(followId)
      .then(() => {
        Toast.info("取消成功", 1);
        changeFollow(userId, null);
        // 删除列表项
        deleteDeviceData(userId, (data: any) => {
          setDataSource(dataSource.cloneWithRows(data));
        });
      })
      .finally(() => {
        setFollowimg(false);
      });
  };

  const changeFollow = (userId: string, followId: any) => {
    const temp = Object.assign({}, followData);
    temp[userId] = followId;
    setFollowData(temp);
  };

  const renderRow = (d: {
    view_my_defense_area_information_id: string;
    view_my_defense_area_information_name: any;
    view_my_defense_area_information_mainFrameName: any;
    view_my_defense_area_information_areaLocationName: any;
    view_my_defense_area_information_status: any;
  }) => (
    <Item
      key={d.view_my_defense_area_information_id}
      title={d.view_my_defense_area_information_name || EMPTY_TEXT}
      mainDescription={`IP端口:${d.view_my_defense_area_information_mainFrameName || EMPTY_TEXT}`}
      secondaryDescription={`区域信息:${d.view_my_defense_area_information_areaLocationName || EMPTY_TEXT}`}
      state={
        <State>
          <DefenseStatus status={d.view_my_defense_area_information_status} />
        </State>
      }
      handle={
        <div
          onClick={e => {
            e.stopPropagation();
            if (followData[d.view_my_defense_area_information_id]) {
              deleteFollow(d.view_my_defense_area_information_id, followData[d.view_my_defense_area_information_id]);
            } else {
              addFollow(d.view_my_defense_area_information_id);
            }
          }}
        >
          {followData[d.view_my_defense_area_information_id] ? <StarSwitch on /> : <StarSwitch />}
        </div>
      }
      onClick={() => {
        history.push(`/protection/detail/${d.view_my_defense_area_information_id}`);
      }}
    />
  );

  return (
    <>
      <EasyNarBar
        mainContent="我的关注"
        leftContent={<Icon type="left" />}
        onLeftClick={() => {
          history.goBack();
        }}
      ></EasyNarBar>

      <ListView
        dataSource={dataSource}
        renderFooter={renderFooter}
        renderRow={renderRow}
        pageSize={NUM_ROWS}
        useBodyScroll
        scrollRenderAheadDistance={500}
        onEndReached={onEndReached}
        onEndReachedThreshold={10}
      />
    </>
  );
});
